*, :before, :after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
    font-family: inherit;
    font-size: inherit;
    text-decoration: none;

    transition-duration: .3s;
    transition-property: none;
}

html, body {
    overflow-y: visible;
}

body {
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    font-size: 17px;
    color: #212121; /* grey 900 */
    background-color: #F5F5F5; /* grey 100 */

    padding: calc(58px * 2) 58px;
}

.hidden {
    display: none !important;
}

.no-style * {
    font: inherit;
}

.button {
    box-sizing: content-box;
    background: none;
    border: none;
    padding: 0.3em;
    margin: 0 0.1em;
    border-radius: 50%;
    cursor: pointer;
    width: 1em;
    height: 1em;

    transition-property: color, background, box-shadow;
}

h1, h2, h3, h4, h5, h6 {
    font-family: "Roboto Condensed", sans-serif;
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.5em;
    font-weight: 300;
    text-transform: uppercase;
}

h3 {
    font-size: 1.3em;
}

input {
    font: inherit;
}

header {
    background-color: #F44336;
    color: #FFF;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 58px;
    text-align: center;
    z-index: 2;
    padding: 0.5em 0;

    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.header_actions {
    position: absolute;
    right: 0;
    top: 0;
    margin: 0.5em;
}

.header_action {
    color: #FFF;
}

.filters {
    width: 200px;
    position: fixed;
    right: 0;
    top: 0;
    height: 100%;
    margin: 58px 0;
    padding: 58px 36px;
    transform: translateX(100%);

    transition-property: transform;
}

.filters-visible .filters {
    transform: translateX(0);
}

.filters label {
    display: block;
    padding-top: 1em;
}

.filters label select, .filters label span {
    vertical-align: middle;
}

.list--episodes {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    margin: 0 auto;

    transition-property: width, transform;
}

.filters-visible .list--episodes {
    width: calc(100% - 200px);
    transform: translateX(calc(-200px / 2));
}

.podcast_image {
    width: 100px;
    height: 100px;
}

cbus-episode {
    display: inline-block;
    flex-shrink: 0;
    position: relative;
    width: 250px;
    height: 250px;
    overflow: hidden;
    color: #FFF;
    margin: 10px;

    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.episode_background {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
}

.episode_innards-container {
    z-index: 1;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), transparent);
    padding: 20px;
}

.episode_audio_player {
    display: none;
}

.episode_audio_button--play {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 20px;

    color: #FFF;
    background-color: #F44336; /* red 500 */

    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    transition-property: box-shadow;
}

.episode_audio_button--play:hover {
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.episode_description-container {
    margin: 10px 0;
    height: 210px;
    overflow-y: auto;
    display: none;
}

.episode_feed-title {
    margin-top: 0.3em;
    font-weight: 400;
}

.player {
    display: flex;
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #4DD0E1; /* cyan 300 */
    color: #000;
    height: 58px;
    z-index: 2;

    box-shadow: 0 -3px 6px rgba(0, 0, 0, 0.16), 0 -3px 6px rgba(0, 0, 0, 0.23);
}

.player_slider {
    width: 500px;
}

.player_buttons {
    display: flex;
    justify-content: center;
    padding-bottom: 0.5em;
}

.player_button {
    margin: 0;
}

.player_button:hover {
    color: #F44336; /* red 500 */
}

.player_episode {
    display: flex;
    width: 300px;
    border-right: solid 1px rgba(0, 0, 0, 0.5);
}

.player_episode_image {
    flex-shrink: 0;
    width: 58px;
    height: 58px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: lightgrey;
}

.player_episode_info {
    padding: 0.5em;
    font-family: "Roboto Condensed", sans-serif;
    overflow: hidden;
}

.player_episode_feed-title {
    color: rgba(0, 0, 0, 0.7);
}

.player_episode_title, .player_episode_feed-title {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.player_current {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: calc(100% - 300px - 173px);
}

.player_slider {
    width: calc(100% - 100px);
}

/* Material styles */

.material-icons {
    font-family: "Material Icons";
    font-weight: normal;
    font-style: normal;
    font-size: 24px;  /* Preferred icon size */
    display: inline-block;
    width: 1em;
    height: 1em;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;

    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;

    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;

    /* Support for IE. */
    font-feature-settings: "liga";
}

.material-icons.md-18 { font-size: 18px; }
.material-icons.md-24 { font-size: 24px; }
.material-icons.md-36 { font-size: 36px; }
.material-icons.md-48 { font-size: 48px; }

.material-icons.md-dark { color: rgba(0, 0, 0, 0.54); }
.material-icons.md-dark.md-inactive { color: rgba(0, 0, 0, 0.26); }

.material-icons.md-light { color: rgba(255, 255, 255, 1); }
.material-icons.md-light.md-inactive { color: rgba(255, 255, 255, 0.3); }

.material-icons.md-inactive { opacity: 0.7; }
